import React, { Component } from 'react';
import {
  NavLink
} from "react-router-dom";
import AuthService from './AuthService';
//const $ = window.$; // esta línea permite que Jquery funcione


// Contact component render contact form
class Signin extends Component {
  constructor(props){
    super(props);
    this.state = {
      contactEmail: '',
      contactPass: ''
    };

    this._handleSubmit = this._handleSubmit.bind(this);
    this._handleChangeEmail = this._handleChangeEmail.bind(this);
    this._handleChangePass = this._handleChangePass.bind(this);
    this._handlePushSignup = this._handlePushSignup.bind(this);

    this.Auth = new AuthService();
   }

  // Change state of input field so text is updated while typing
  _handleChangeEmail(e) {
    this.setState({
      contactEmail: e.target.value,
    });
  }
  // Change state of input field so text is updated while typing
  _handleChangePass(e) {
    this.setState({
      contactPass: e.target.value
    });
  }

  // When push Signup Link
  _handlePushSignup(e) {
    this.props.history.push("/signin");
  }

  _handleSubmit(e) {
    e.preventDefault();
    // this.setState({
    //   contactEmail: '',
    //   contactPass: ''
    // });

    this.Auth.login(this.state.contactEmail,this.state.contactPass)
          .then(res =>{
             this.props.history.replace('/');
          })
          .catch(err =>{
              alert(err);
          });
    }



  render() {
    return (
      <div className="contact" id="contact">
        <div className="filter">
          <h2>Inicia sesión</h2>
          <form className="form" onSubmit={this._handleSubmit} id="formContact">  
            <label>Correo Electrónico:</label>
            <input id="formEmail" type="email" placeholder="Correo electrónico" name="formEmail" value={this.state.contactEmail} onChange={this._handleChangeEmail} required/>
            <label>Contraseña:</label>
            <input type="password" id="formPass" placeholder="Contraseña" name="formPass" value={this.state.contactPass} onChange={this._handleChangePass} required></input>
            <p><input type="submit" value="Iniciar Sesión" className="btn--cta" id="btn-submit" /></p>
            <p><NavLink to="/signup">Crea una cuenta</NavLink></p>
            <p><NavLink to="/forgotpass">Olvidé mi contraseña</NavLink></p>
          </form>
        </div>
      </div>
    )
  }

  componentWillMount(){
    if(this.Auth.loggedIn())
        this.props.history.replace('/');
  }

}

export default Signin;