import React, { Component } from 'react';
//const $ = window.$; // esta línea permite que Jquery funcione


// Contact component render contact form
class Signup extends Component {
  constructor(props){
    super(props);
    this.state = {
      contactEmail: '',
      contactPass: '',
      contactName: '',
      contactLastName: ''
    };

    this._handleSubmit = this._handleSubmit.bind(this);
    this._handleChange = this._handleChange.bind(this);
    this._handleChangePass = this._handleChangePass.bind(this);
    this._handleChangeName = this._handleChangeName.bind(this);
    this._handleChangeLastName = this._handleChangeLastName.bind(this);
  }

  // Change state of input field so text is updated while typing
  _handleChange(e) {
    this.setState({
      contactEmail: e.target.value,
    });
  }
  // Change state of input field so text is updated while typing
  _handleChangePass(e) {
    this.setState({
      contactPass: e.target.value
    });
  }

  _handleChangeName(e) {
    this.setState({
      contactName: e.target.value
    });
  }

  _handleChangeLastName(e) {
    this.setState({
      contactLastName: e.target.value
    });
  }

  _handleSubmit(e) {
    e.preventDefault();
    this.setState({
      contactEmail: '',
      contactPass: '',
      contactName: '',
      contactLastName: ''
    });


    const xhr = new XMLHttpRequest();
    //const file = this.state.fileName;
    var domainURL = process.env.NODE_ENV !== "production" ? 'http://localhost:3000' : "https://apptd.herokuapp.com";
    xhr.open('POST', `${domainURL}/auth/signup`);
    //xhr.setRequestHeader('Authorization', 'Bearer ' + this.getToken());
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 201){
          const response = JSON.parse(xhr.responseText);
          // todo: quitar este log
          console.log(response);
          this.setState({
            contactEmail: 'success',
            contactPass: ''
          });
          localStorage.setItem("id_token", response.token);
          this.props.history.replace('/');
          //this.uploadFile(file, response.signedRequest, response.url);
        }
        else{
          alert('No se pudo crear la cuenta');
        }
      }
    };
    xhr.send(`email=${this.state.contactEmail}&password=${this.state.contactPass}&name=${this.state.contactName}&lastname=${this.state.contactLastName}`);

    // $(document).ready(function(){
    //   $.ajax({
    //     url: process.env.NODE_ENV !== "production" ? 'https://apptd.herokuapp.com/auth/signup' : "https://apptd.herokuapp.com/auth/signup",
    //     type: 'POST',
    //     data: {
    //       'email': this.state.contactEmail,
    //       'password': this.state.contactPass,
    //       'name': this.state.contactName,
    //       'lastname': this.state.contactLastName
    //     },
    //     cache: false,
    //     success: function(data) {
    //       // Success..
    //       // TODO: Navigate next page or homepage
    //       this.setState({
    //         contactEmail: 'success',
    //         contactPass: ''
    //       });
    //       //$('#formContact').slideUp();
    //       //$('#formContact').after(this.state.contactPass);
    //       console.log('success', data);
    //       localStorage.setItem("id_token", data.token);
    //     }.bind(this),
    //     // Fail..
    //     error: function(xhr, status, err) {
    //       console.log(xhr, status);
    //       console.log(err);
    //       this.setState({
    //         contactEmail: 'danger',
    //         contactPass: ''
    //       });
    //       console.log(this.state.contactEmail + this.state.contactPass + 'fail');
    //     }.bind(this)
    //   });
    // });

    
  }

  render() {
    return (
      <div className="contact" id="contact">
        <div className="filter">
          <h2>Completa tus datos</h2>
          <form className="form" onSubmit={this._handleSubmit} id="formContact">
            <label>Nombre:</label>
            <input id="formName" type="text" placeholder="Nombre" name="formName" value={this.state.contactName} onChange={this._handleChangeName} required/>
            <label>Apellido:</label>
            <input id="formLastName" type="text"placeholder="Apellido" name="formLastName" value={this.state.contactLastName} onChange={this._handleChangeLastName} required/>
            <p></p>
            <label>Correo Electrónico:</label>
            <input id="formEmail" type="email" placeholder="Correo electrónico" name="formEmail" value={this.state.contactEmail} onChange={this._handleChange} required/>
            <label>Contraseña:</label>
            <input type="password" placeholder="Contraseña" id="formPass" name="formPass" value={this.state.contactPass} onChange={this._handleChangePass} required></input>
            <p><input type="submit" value="Crear Cuenta" className="btn--cta" id="btn-submit" /></p>
          </form>
        </div>
      </div>
    )
  }
}

export default Signup;