import React, { Component } from "react";
import AuthService from './AuthService';

class UploadFiles extends Component {

  constructor(props){
    super(props);
    this.state = {
      fileName: null,
      garmentName: "",
      price: 0,
      size: "",
      color: "",
      loaded: 0
    };

    this._handleChangeFile = this._handleChangeFile.bind(this);
    this._handleChangeGarmentName = this._handleChangeGarmentName.bind(this);
    this._handleChangePrice = this._handleChangePrice.bind(this);
    this._handleChangeSize = this._handleChangeSize.bind(this);
    this._handleChangeColor = this._handleChangeColor.bind(this);

    this._handleUpload = this._handleUpload.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
    this.Auth = new AuthService();

  }

  _handleChangeFile(e) {
    this.setState({
      fileName: e.target.files[0],
      loaded: 0,
    });
    console.log(e.target.files[0]);
  }

  _handleChangeGarmentName(e) {
    this.setState({
      garmentName: e.target.value
    });
  }

  _handleChangePrice(e) {
    this.setState({
      price: e.target.value
    });
  }

  _handleChangeSize(e) {
    this.setState({
      size: e.target.value
    });
  }

  _handleChangeColor(e) {
    this.setState({
      color: e.target.value
    });
  }
 
  _handleUpload(e) {
  const xhr = new XMLHttpRequest();
  const file = this.state.fileName;
  xhr.open('GET', `https://apptd.herokuapp.com/sign-s3-single?filename=${file.name}&filetype=${file.type}`);
  xhr.setRequestHeader('Authorization', 'Bearer ' + this.getToken());
  xhr.onreadystatechange = () => {
    if(xhr.readyState === 4){
      if(xhr.status === 200){
        const response = JSON.parse(xhr.responseText);
        console.log(response);
        console.log(file);
        this.uploadFile(file, response.signedRequest, response.url);
      }
      else{
        alert('Could not get signed URL.');
      }
    }
  };
  xhr.send();
  }

  uploadFile(file, signedRequest, url){
    const xhr = new XMLHttpRequest();
    xhr.open('PUT', signedRequest);
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 200){
          document.getElementById('preview').src = url;
          document.getElementById('avatar-url').value = url;
          this.uploadItem(file, signedRequest, url);
        }
        else{
          alert('Could not upload file.');
        }
      }
    };
    xhr.send(file);
  }

  uploadItem(file, signedRequest, url){
    const xhr = new XMLHttpRequest();
    var domainURL = process.env.NODE_ENV !== "production" ? 'http://localhost:3000' : "https://apptd.herokuapp.com";
    xhr.open('POST', `${domainURL}/items`);
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    xhr.setRequestHeader('Authorization', 'Bearer ' + this.getToken());
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 201){
          alert('Se dio de alta el item.');
        }
        else{
          alert('Could not upload file.');
        }
      }
    };
    xhr.send(`color=${this.state.color}&size=${this.state.size}&url=${url}&fileName=${file.name}&price=${this.state.price}&garmentName=${this.state.garmentName}&fileType=${file.type}`);
  }

  getToken() {
        // Retrieves the user token from localStorage
        return localStorage.getItem('id_token')
  }

  render() {
    return (
      <div>
        <h2>Cuelga tus prendas en tu closet virtual</h2>
        <br/>
        <input type="text" id="garment-input" placeholder="Nombre de la prenda" onChange={this._handleChangeGarmentName} />
        <input type="text" id="price-input" placeholder="Precio de la prenda" onChange={this._handleChangePrice} />
        <br/>
        <input type="text" id="size-input" placeholder="Talla de la prenda" onChange={this._handleChangeSize} />
        <input type="text" id="color-input" placeholder="Color de la prenda" onChange={this._handleChangeColor} />
        <br/>
        <input type="file" id="file-input" onChange={this._handleChangeFile} />
        <img id="preview" src="/images/default.png" alt=""/>
        <input type="hidden" id="avatar-url" name="avatar-url" value="/images/default.png"/>
        <button onClick={this._handleUpload}>Cargar prenda</button>
        <div> {Math.round(this.state.loaded,2) } %</div>
      </div>
    );
  }

  componentWillMount(){
    if(!this.Auth.loggedIn()){
      alert("Favor de iniciar sesión con tu usuario");
        this.props.history.replace('/signin');
    }
        
  }

}
 
export default UploadFiles;