import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./Home";
import Signup from "./Signup";
import Signin from "./Signin";
import ForgotPass from "./ForgotPass";
import UploadFiles from "./UploadFiles";
import Rents from "./Rents";
import Thanks from "./Thanks";
import Modal, {closeStyle} from 'simple-react-modal';
import AuthService from './AuthService';



class Main extends Component {
    constructor(props){
      super(props);
      this.state = {};
      this.Auth = new AuthService();
    }      

    show(){
      this.setState({show:true});
    }
    close(){
      this.setState({show:false});
    }
    logout(){
      this.Auth.logout();
    }
    render() {
      return (
        <HashRouter>
          <div>
          
            <Modal show={this.state.show} onClose={this.close.bind(this)} transitionSpeed={600}>
              <div>hey, click outside of me to close me!</div>
              <a style={closeStyle} onClick={this.close.bind(this)}>X</a>
            </Modal>

            <h1>The Dresser </h1>
            <NavLink to="/Signin">Iniciar Sesión</NavLink>
            <br/>
            <NavLink to="" onClick={this.logout.bind(this)} >Cerrar Sesión</NavLink>
            <ul className="header">
              <li><NavLink to="/">Inicio</NavLink></li>
              <li><NavLink to="/signup">Crear Cuenta</NavLink></li>
              <li><NavLink to="/uploadfiles">Carga tus prendas</NavLink></li>
              <li><NavLink to="/rents">Listado de rentas</NavLink></li>
            </ul>
            <div className="content">
              <Route exact path="/" component={Home}/>
              <Route path="/signup" component={Signup}/>
              <Route path="/signin" component={Signin}/>
              <Route path="/forgotpass" component={ForgotPass}/>
              <Route path="/uploadfiles" component={UploadFiles}/>
              <Route path="/rents" component={Rents}/>
              <Route path="/thanks" component={Thanks}/>
            </div>
          </div>
        </HashRouter>
      );
    }
  }
 
export default Main;