import React, { Component } from 'react';

// Contact component render contact form
class ForgotPass extends Component {
  constructor(props){
    super(props);
    this.state = {
      contactEmail: ''
    };

    this._handleSubmit = this._handleSubmit.bind(this);
    this._handleChangeEmail = this._handleChangeEmail.bind(this);
  }

  // Change state of input field so text is updated while typing
  _handleChangeEmail(e) {
    this.setState({
      contactEmail: e.target.value,
    });
  }
  
  _handleSubmit(e) {
    e.preventDefault();
    var domainURL = process.env.NODE_ENV !== "production" ? 'http://localhost:3000' : "https://apptd.herokuapp.com";
    const xhr = new XMLHttpRequest();
    var domainURL = process.env.NODE_ENV !== "production" ? 'http://localhost:3000' : "https://apptd.herokuapp.com";
    xhr.open('POST', `${domainURL}/auth/forgotPass`);
    xhr.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
    //xhr.setRequestHeader('Authorization', 'Bearer ' + this.Auth.getToken());
    xhr.onreadystatechange = () => {
      if(xhr.readyState === 4){
        if(xhr.status === 201){
          alert('Se enviaron los pasos a tu correo para restablecer la contraseña.');
        }
        else{
          alert('No se pudo enviar el correo.');
        }
      }
    };
    xhr.send(`email=${this.state.contactEmail}`);
 
  }

  
  render() {
    return (
      <div className="contact" id="contact">
        <div className="filter">
        <h2>Recupera tu contraseña</h2>
          <form className="form" onSubmit={this._handleSubmit} id="formContact">
            <label>Correo Electrónico:</label>
            <input id="formEmail" type="email" placeholder="Correo electrónico" name="formEmail" value={this.state.contactEmail} onChange={this._handleChangeEmail} required/>
            <p><input type="submit" value="Enviar correo" className="btn--cta" id="btn-submit" /></p>
          </form>
        </div>
      </div>
    )
  }
}

export default ForgotPass;